html,
body {
  font-family: "Roboto Condensed", sans-serif; }

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
footer,
header,
nav,
section {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

figcaption,
figure,
main {
  display: block; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: inherit; }

b,
strong {
  font-weight: bolder; }

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

dfn {
  font-style: italic; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

audio,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  display: inline-block;
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details,
menu {
  display: block; }

summary {
  display: list-item; }

canvas {
  display: inline-block; }

template {
  display: none; }

[hidden] {
  display: none; }

.main-header {
  background: #eee;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end; }
  .main-header .logo {
    margin-right: auto;
    padding-left: 50px; }
  .main-header .personal-info-container {
    color: #212121;
    padding-right: 50px;
    letter-spacing: 1.3px; }
    .main-header .personal-info-container .name {
      font-size: 2em;
      margin-bottom: 0; }
    .main-header .personal-info-container .role {
      font-size: 1.25em;
      margin: 0;
      padding: 15px 0; }

.divider {
  width: 100vw;
  height: 80px;
  background: #2d3c49; }

@media screen and (max-width: 38em) {
  .logo,
  .personal-info-container {
    padding: 15px !important; }
  .name {
    font-size: 1.25em !important; }
  .role {
    font-size: 1em !important; } }

.hero-image {
  margin: 15px 0;
  height: 300px;
  background-image: url(/dist/assets/img/hero-image.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed; }

@media screen and (max-devide-width: 600px) {
  background-attachment: scroll; }

#home {
  max-width: 900px;
  margin: auto; }

.section-title {
  font-size: 1.5em;
  color: #7d97ad;
  letter-spacing: 1.3px; }

.grid-wrapper {
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr); }

.card {
  background: snow;
  border-radius: 5px;
  box-shadow: 7px 7px 15px rgba(33, 33, 33, 0.7);
  border: 1px solid #2d3c49;
  transition: all 400ms ease-in-out; }
  .card:hover {
    transform: translateY(-3px);
    box-shadow: 12px 14px 15px rgba(33, 33, 33, 0.7); }
  .card .project-img {
    width: 100%;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px; }
  .card .responsive-img {
    width: 100%;
    height: 200px; }
  .card .project-title {
    padding: 15px;
    font-size: 1.25em;
    margin: 0;
    letter-spacing: 1.3px;
    color: #212121;
    text-transform: uppercase; }
  .card .project-desc {
    font-size: 1em;
    margin: 0;
    padding: 15px;
    letter-spacing: 1.2px; }

@media screen and (max-width: 60em) {
  #home {
    max-width: 600px; }
  .grid-wrapper {
    grid-template-columns: 1fr 1fr !important; } }

@media screen and (max-width: 38em) {
  .section-title {
    padding-left: 15px !important; }
  .grid-wrapper {
    grid-template-columns: 1fr !important;
    margin: 0 40px; } }

.footer {
  background: #2d3c49;
  margin-top: 30px;
  height: 80px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  letter-spacing: 2px; }
